@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@600&family=Changa:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

   @media  only screen and (min-width : 900px) {
          .custom-container {
            display: block !important;
            width: 70% !important;
            flex:none !important;
            margin-left: auto;
margin-right: auto;
          }
      }


 body {
    padding-top: 80px;
  }

.oc {
  margin-left: 5px;
  margin-right: 4px;
  width: 30%;
}

.card-img-overlay {
  position: absolute ;
    top: -10px !important;
    left: -10px !important;
    right: -10px !important;
    z-index: 1 !important;
}

.opencritic{
  margin-left:  none;
  border-radius: 5px;
  background-color: #f5f5f5;
  border: 1px solid #e5e5e5;
 width: 50px;
  background: rgba(0%, 0%, 0%, 0.5); 
}

.custom-container {
  border-radius: .5rem .5rem .5rem .5rem;
  padding: .75rem 1.25rem;
  background-color: #ffffff;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.15);
  margin-bottom: 30px;
}

.searchbox {
  width: 40%;
  border-radius: 5rem;
  padding: 10px;
  border: 1px solid #55597d;
  box-shadow: 0 3px 10px rgb(0 0 0 / 0.15);
  margin:  10px;
  outline: none;
}

textarea:hover, 
input:hover, 
textarea:active, 
input:active, 
textarea:focus, 
input:focus,
button:focus,
button:active,
button:hover,
label:focus,
.btn:active,
.btn.active
{
    outline:none !important;
    -webkit-appearance:none;
}

::-webkit-input-placeholder {
	font-size: 15px;
	font-family: 'Font Awesome 5 Free', 'Cairo';
	color: #55597d ;
}


.card-img{
  border-radius: 5px !important;
}

.card-title{
  font-size: 18px !important;
}

 .card-text{
    font-size:14px !important;
    color: white;
    font-weight: bold;
}


.nbadges {
    width: 30px;
    height: 30px;
    border-radius: 100%;
    background-image: url(/reactmaysaleba/static/media/platform-icons18.518321f0.webp);
    background-repeat: no-repeat;
    background-size: 30px;
    display: inline-block;
    flex: none;
}

.nbadges.nintendo {
    background-color: #e70009;
    background-position: 0px -510px;
}


@media screen and (max-width: 900px) {
  .custom-container {
            display: block !important;
            width: 90% !important;
            flex:none !important;
            margin-left: auto;
			margin-right: auto;
          }

  body {
    padding-top: 60px;
  }

  .searchbox {
	width: 80%;
}

 .card-title{
    font-size:1rem !important;
}

.card-text{
    font-size:10px !important;
}
          }


